import React from "react";
import { Col, Row, Spin } from "antd";
import { CTitleLoading } from "..";

const CLoadingSpinner = () => {
  return (
    <Row
      justify="center"
      style={{
        textAlign: "center",
        alignItems: "center",
        height: "100vh",
      }}
    >
      <CTitleLoading />
      <Col span={24}>
        <Spin size="large" />
      </Col>
    </Row>
  );
};

export default CLoadingSpinner;
